export default{
	methods: {
		showNotif(message){
			this.$swal(message);
		},
		showLoading(){
			this.$swal({
				title: 'Memuat',
				html: "<h2><i class='fas fa-spin fa-spinner'></i></h2>",
				allowOutsideClick: false,
				showCancelButton: false,
				showConfirmButton: false
			});
		},
		hideLoading(){
			this.$swal.close();
		},
		showAlert(message, type = 'info'){
			this.$toast.open({
				message: message,
				type: type,
				position: 'top-right',
			});
		}
	}
}