<template>
	<Dashboard>
		<div>
			<div class="mb-4 text-white">
				<h5>Kelola Pengguna</h5>
				<small>Kelola Pengguna - <b>Pengguna</b></small>
			</div>

			<div class="card">
				<div class="card-body">

					<div id="table-wrapper" class="mt-2">
						<table class="table table-bordered">
							<thead class="text-center">
								<tr>
									<th>No</th>
									<th>Nama Principal</th>
									<th>Nama Lengkap</th>
									<th>Nickname</th>
									<th>Departement</th>
									<!-- <th>Tindakan</th> -->
								</tr>
							</thead>
							<tbody>
								<tr v-for="(v,i) in users" :key="i">
									<td>{{ i+1 }}</td>
									<td>{{ v.principal }}</td>
									<td>{{ v.fullname }}</td>
									<td>{{ v.nickname }}</td>
									<td>{{ v.department }}</td>
									<!-- <td>
										<button @click="showEdit" class="btn btn-sm btn-warning mr-1"><i class="fas fa-edit"></i></button>
										<button @click="showDelete"  class="btn btn-sm btn-danger"><i class="fas fa-times-circle"></i></button>
									</td> -->
								</tr>
							</tbody>
						</table>
					</div>

				</div>
			</div>
		</div>

		
		<div class="modal fade" id="modalAdd" tabindex="-1" role="dialog" aria-labelledby="alokasiTitle" aria-hidden="true">
			<div class="modal-dialog" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLongTitle">Tambah Pengguna</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">

						<div class="form-group">
							<label>Nama Lengkap</label>
							<input type="text" class="form-control" v-model="model.fullname">
						</div>
						<div class="form-group">
							<label>Nama Panggilan</label>
							<input type="text" class="form-control" v-model="model.nickname">
						</div>
						<div class="form-group">
							<label>Email</label>
							<input type="text" class="form-control" v-model="model.email">
						</div>
						<div class="form-group">
							<label>Username</label>
							<input type="text" class="form-control" v-model="model.username">
						</div>
						<div class="form-group">
							<label>Password</label>
							<input type="password" class="form-control" v-model="model.password">
						</div>

					</div>

					<div class="modal-footer">
						<button type="button" class="btn btn-secondary" data-dismiss="modal">Tutup</button>
						<button type="button" @click="doAdd" class="btn btn-success">Simpan</button>
					</div>
				</div>
			</div>
		</div>

		<div class="modal fade" id="modalRole" tabindex="-1" role="dialog" aria-labelledby="alokasiTitle" aria-hidden="true">
			<div class="modal-dialog" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLongTitle">Atur Peran</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">

						<div class="form-group">
							<label>Pilih Peran</label>
							<select class="form-control">
								<option>Superuser</option>
							</select>
						</div>

					</div>

					<div class="modal-footer">
						<button type="button" class="btn btn-secondary" data-dismiss="modal">Tutup</button>
						<button type="button" @click="doEdit" class="btn btn-success">Simpan</button>
					</div>
				</div>
			</div>
		</div>

		<div class="modal fade" id="modalDelete" tabindex="-1" role="dialog" aria-labelledby="alokasiTitle" aria-hidden="true">
			<div class="modal-dialog" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLongTitle">Hapus Pengguna</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">

						<div class="form-group">
							<label>Alasan</label>
							<textarea class="form-control"></textarea>
						</div>

						<div class="form-group">
							<input type="checkbox" v-model="isChecked"> Dengan ini saya menyatakan bahwa saya sadar untuk menghapus pengguna ini dari SIAPIN
						</div>

					</div>

					<div class="modal-footer">
						<button type="button" class="btn btn-secondary" data-dismiss="modal">Tutup</button>
						<button type="button" :disabled="!isChecked" @click="doDelete" class="btn btn-danger">Hapus</button>
					</div>
				</div>
			</div>
		</div>

	</Dashboard>
</template>

<script>

	import alertMixin from '@/mixins/alert'
	import { userAll } from '@/api/user'

	export default{
		name: 'User',
		mixins: [
		alertMixin,
		],
		data() {
			return {
				isChecked: false,
				users: [],
				permissions: [],
				model: {
					fullname: null,
					nickname: null,
					email: null,
					username: null,
					password: null,
				}
			}
		},
		mounted(){
			this.getUsers();
		},
		methods: {
			async getUsers(){
				this.users = [];
				this.showLoading();
				try{
					let request = await userAll();
					this.hideLoading();
					let data = request.data;
					data.forEach((v,i) => {
						let user = v.properties;
						if(user.userprincipalname && user.userprincipalname.length > 0){
							this.users.push({
								principal: user.userprincipalname[0],
								nickname: user.givenname[0],
								fullname: user.displayname[0],
								department: user.department[0],
							})
						}
					})

					console.log(request.data);
				}catch(e){
					console.log(e);
					this.hideLoading();
					this.showAlert("Gagal Mendapatkan Data", "error");
					if(e.response?.status == 401){
						this.invalidSession();
					}
				}
			},
			showAdd(){
				this.model.fullname = null;
				this.model.nickname = null;
				this.model.username = null;
				this.model.password = null;
				this.model.email = null;
				$("#modalAdd").modal('show');
			},
			showEdit(){
				$("#modalRole").modal('show');
			},
			doAdd(){
				$("#modalAdd").modal('hide');
				this.$toast.open({
					message: 'User Added Successfully !',
					type: 'info',
					position: 'top-right',
				});
			},
			doEdit(){
				$("#modalRole").modal('hide');
				this.$toast.open({
					message: 'Role Edited Successfully Successfully !',
					type: 'info',
					position: 'top-right',
				});
			},
			showDelete(){
				$("#modalDelete").modal('show');
			},
			doDelete(){
				$("#modalDelete").modal('hide');
				this.$toast.open({
					message: 'User Deleted Successfully !',
					type: 'info',
					position: 'top-right',
				});
			},
			async invalidSession(){
				alert("Sesi sudah tidak valid, silahkan login");
				await this.$store.dispatch("auth/authLogout");
				this.$router.replace("/login");
			}
		}
	}
</script>